[contenteditable=true] {
  background-color: rgba(0,0,0,.03);
  border-radius: 2px;
  padding-left: 5px;
  padding-right: 5px;
  border:rgba(0,0,0,.08);
  border-width: 0.5px;
  border-style: solid;
  min-height: 36px;
  padding-top: 5px;
  padding-bottom: 5px;
}

div:hover[contenteditable="true"] {
  background-color: rgba(0,0,0,.08);
  color: #0D8590;
}

div.testField {
  background-color: rgba(0,0,0,.03);
  border-radius: 2px;
  padding-left: 5px;
  padding-right: 5px;
  border:rgba(0,0,0,.08);
  border-width: 0.5px;
  border-style: solid;
  -webkit-user-modify: read-write;
    overflow-wrap: break-word;
    -webkit-line-break: after-white-space;
}

div.testField:hover {
  background-color: rgba(0,0,0,.08);
  color: #0D8590;
}

[contenteditable=true]:hover:empty:before {
  background-color: rgba(0,0,0,.08);
  color: #0D8590;
}

div.lineage_model_node {
  background-color: #F36D6D;
  border-color: #800000;
}
div.lineage_source_node {
  background-color: #3DCCCC;
  border-color: #0D8590;
}

#main .navbar {
  padding-top: 0px;
  padding-bottom: 0px;
  height: 56px;
}

div.react-bootstrap-drawer {
  z-index: 0;
  overflow:scroll;
}


.treeview > .toggle {
  pointer-events: all;
  cursor: pointer;
}
.treeview > .toggle:hover {
  color: #63b1de;
  fill: #63b1de;
}

.catalogColumnName {
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  max-width: 20vw;
  display: block;
}

.catalogColumnName:hover {
  text-overflow: clip;
  white-space: normal;
  word-break:break-all;
}

.catalogColumnType {
  text-overflow: ellipsis;
  overflow: hidden;
}

.catalogColumnType:hover {
  text-overflow: clip;
  white-space: normal;
  word-break:break-all;
}

li.rstm-tree-item {
  text-overflow: ellipsis;
  overflow: hidden;
}

li.rstm-tree-item:hover {
  text-overflow: clip;
  white-space: normal;
  word-break:break-all;
  background-color: #095E66;
  color:white;
}

li.rstm-tree-item--active {
  background: #0D8590;
}

button.tealButton {
  background-color: #0D8590;
  color: white;
}

button.tealButton:hover {
  background-color: #095E66;
  color: white;
}

button.tealButton:focus {
  background-color: #0D8590;
  color: white;
}

button.tealButton:focus:hover {
  background-color: #095E66;
  color: white;
}

button.btn-primary.tealButton:not(:disabled):active { /*This has to be this specific to match*/
  background-color: #0D8590;
  color: white;
}

.treeTabs > a {
  color: #0D8590;
}

